import React, { useState, useEffect, useContext } from 'react'
import { socket as importedSocket } from '../socket'
import { ScriptBoard } from '../modals/ChessModal_back'

const SocketContext = React.createContext()

export function useSocket() {
    return useContext(SocketContext)
}

export function SocketProvider({ children }) {
    const [message, setMessage] = useState('')
    const [username,  ] = useState('guest')
    const [connected, setConnected] = useState(false)
    const [socket,  ] = useState(importedSocket)
    const [room, setRoom] = useState(null)
    const [otherMembers, setOtherMembers] = useState([])
    const [options, setOptions] = useState(null)
    const [board, setBoard] = useState(ScriptBoard.Init)
    const [inTurn, setInTurn] = useState(false)
    const [lastMove, setLastMove] = useState(null)
    const [chatMessage, setChatMessage] = useState({})

    function sendMessage(msg) {
        socket?.emit('room:chat', room, msg)
    }
    function onChatMessage(sender, msg) {
        setChatMessage(() => ({
            sender,
            msg}))
    }
    function onStartTheFight(msg, options) {
        setMessage(() => `${msg}. Match info: type: ${options.typeOfMatch}, totalTime: ${options.totalTime}, goFirst: ${options.goFirst}`)
        setOptions(() => options)
        setBoard(() => options.board)
        // console.log("init board", options.board)
        setInTurn(() => options.goFirst === true)
    }
    function onEndTheFight(msg) {
        setMessage(() => `${msg}`)
        setOptions(() => null)
        setInTurn(() => false)
        setRoom(() => null)
        setBoard(() => ScriptBoard.Blank)
    }
    function onChessBoard(fromRoom, curBoard, yourTurn, lastMove) {
        if (room !== fromRoom) return
        setLastMove(() => lastMove)
        setBoard(() => curBoard)
        setInTurn(() => yourTurn)
    }
    function updateBoardOnServer(room, board, lastMove) {
        if (room === null || board === null) return
        socket.emit('chess-board', room, board, lastMove)
    }
    function onConnect() {
        setMessage('Connect to server successfully!')
        setConnected(true);
    }

    function onDisconnect() {
        setConnected(false);
    }
    function onFoundATable(msg, inRoom, opponent) {
        const info = `${msg} in room: ${inRoom}`
        console.log('info: ', info)
        setMessage(() => info)
        setRoom(() => inRoom)
        console.log('opponent', opponent)
        setOtherMembers(currentPlayers => [...currentPlayers, opponent])
        //join in the created room
    }
    function onNotification(data) {
        console.log('notification from server ', data)
    }

    function findATable() {
        if (!connected) connect()
        setMessage('Send a signal to server to find a table')
        socket?.emit('find-a-table', 'wanna play')
    }

    function playWithComputer() {
        if (!connected) connect()
        setMessage('Send a signal to server to find a table')
        socket?.emit('play-with-computer', 'wanna play')
    }

    function connect() {
        socket.auth = { username: username }
        socket.connect();
    }

    function disconnect() {
        socket.disconnect();
    }

    function notifyReady() {
        socket?.emit('ready-for-a-fight', room)
    }

    function notifyLeaving() {
        socket?.emit('leave-room', room)
    }

    function onRoomNotification(data) {
        setMessage(() => `room-notification:${data}`)
    }
    

    useEffect(() => {
        socket?.on('room-notification', onRoomNotification)
        socket?.on('start-the-fight', onStartTheFight)
        socket?.on('end-the-fight', onEndTheFight)
        socket?.on('chess-board', onChessBoard)
        socket?.on('connect', onConnect);
        socket?.on('disconnect', onDisconnect);
        socket?.on('notification', onNotification)
        socket?.on('found-a-table', onFoundATable)
        socket?.on('room:chat', onChatMessage)
        
        return () => {
            socket?.off('room-notification', onRoomNotification)
            socket?.off('start-the-fight', onStartTheFight)
            socket?.off('end-the-fight', onEndTheFight)
            socket?.off('chess-board', onChessBoard)
            socket?.off('connect', onConnect);
            socket?.off('disconnect', onDisconnect);
            socket?.off('notification', onNotification)
            socket?.off('found-a-table', onFoundATable)
            socket?.off('room:chat', onChatMessage)
        }
    }, [connected, socket, room, board, inTurn, chatMessage])

    const value = {
        message,
        socket,
        connected,
        room,
        board, setBoard, updateBoardOnServer,
        findATable, playWithComputer,
        connect,
        disconnect,
        otherMembers,
        notifyReady, notifyLeaving,
        inTurn,
        options,
        lastMove,
        chatMessage,
        sendMessage
    }
    return (
        <SocketContext.Provider value={value}>
            {children}
        </SocketContext.Provider>
    )
}
