const getPawnType = (piece, pos) => {
    let { type } = getPieceByValue(piece)
    if ((type & PieceMask) === Type.Pawn) {
        if (pos < 48 && (type & ColorMask) === Color.Black) return Type.MovedPawn | Color.Black
        if (pos > 15 && (type & ColorMask) === Color.White) return Type.MovedPawn | Color.White
    }
    return type
}
