const MoveMode = {
    Attack: 1,
    Move: 2,
    Both: 3
}

const getPieces = (cell) => {
    let pieces = []
    let color = cell & ColorMask
    let size = BitSizeOfPiece
    for (let i = 0; i < size; i++) {
        let piece = firstBitOn << i
        if ((cell & piece) === piece) pieces.push(piece | color)
    }
    return pieces
}

function scriptToBoard(scriptBoard, boardBuffer) {
    if (!scriptBoard) return null
    let board = [...scriptBoard]
    const boardView = new Uint32Array(boardBuffer)
    for (let i = 0; i < Board.Size; i++) {
        boardView[i] = getPiece(board[i]).value;
    }
    return boardBuffer
}
function boardToScript(boardBuffer) {
    if (!boardBuffer) return ScriptBoard.Blank;
    let scriptBoard = ''
    const boardView = new Uint32Array(boardBuffer)
    for (let i = 0; i < Board.Size; i++) {
        scriptBoard += getPieceByValue(boardView[i]).key;
    }
    return scriptBoard
}
