//the piece at pos with color is a target of the opponent
function isBeingControlled(color, boardBuffer, pos) {
    let opponentColor = getOpponentColor(color)
    let movesBuffer = computeBoard(opponentColor, boardBuffer)
    let movesView = new Uint32Array(movesBuffer)

    if (movesView[pos]) return true
    return false
}
function findPos(piece, boardBuffer) {
    let boardView = new Uint32Array(boardBuffer)
    for (let i = 0; i < Board.Size; i++) {
        if (boardView[i] === piece) return i
    }
    return -1
}
//a move script: 'From - To'
//return { from, to }
function parseMove(moveScript) {
    let moves = moveScript?.split(' - ') ?? null
    if (moves) return { from: parseInt(moves[0]), to: parseInt(moves[1]) }
    return null
}
//this function is light different with isCheckmate
function makeAMove(level, color, scriptBoard, scriptLastMove) {
    //use a new boar buffer to separate different boards
    const tempBoardBuffer = new ArrayBuffer(Board.Size * Board.CellSize)
    let boardBuffer = scriptToBoard(scriptBoard, tempBoardBuffer)
    let boardView = new Uint32Array(boardBuffer)
    // const tempMovesBuffer = new ArrayBuffer(Board.Size * Board.CellSize)

    let lastMove = parseMove(scriptLastMove)

    //check if myKing is being checked or not
    let myKing = Piece.King | color
    let opponentColor = getOpponentColor(color)

    if (isBeingChecked(boardBuffer, myKing)) {
        // console.log('is being check')

        //the king is targetting
        //check if the opponent checkmates the king (2 steps)

        //////
        //which move should be considered first?

        //1. check if the king can avoid being checked itself
        //calculate all possible moves of opponent pieces
        let opponnentMovesBuffer = populateBoard(opponentColor, boardBuffer, lastMove)
        let opponnentMovesView = new Uint32Array(opponnentMovesBuffer)

        //calculate all possible moves of pieces
        let movesBuffer = computeBoard(color, boardBuffer, lastMove)
        let movesView = new Uint32Array(movesBuffer)
        let kingPos = findPos(myKing, boardBuffer)
        for (let i = 0; i < Board.Size; i++) {
            if ((movesView[i] & Piece.King) === Piece.King //my king moves
                && opponnentMovesView[i] === 0) //is not controlled by opponent pieces
            {
                //test that move
                if (testTheWater(boardView, boardBuffer, myKing, kingPos, i)) {
                    //make the move to save my king
                    moveTo(boardView, boardBuffer, kingPos, i)
                    // boardView[i] = boardView[kingPos]
                    // boardView[kingPos] = 0
                    return boardToScript(boardBuffer)
                }
            }
        }
        //king can not avoid being checked itself
        console.log('king can not avoid being checked itself')
        console.log('check if there is any piece can intercept the situation')
        //check if there is any piece can intercept the situation

        //2. check if there is any piece can intercept the checking path to save my king
        //get attackers who are checking my king (can attack my king position)

        //ignore the king in the attacker list
        //speed up by checking the attacker nearby the king: capturing the attacker is the only way to save the king
        let attackers = getPieces(opponnentMovesView[kingPos])
        for (let j = 0; j < attackers.length; j++) {
            //find the attacker position
            // let attackerPos = findPos(attackers[j], boardBuffer)
            // if (attackerPos === kingPos) continue;

            //speed up by traking the checking path
            for (let i = 0; i < Board.Size; i++) {
                if ((((attackers[j]) === boardView[i])//position of the attacker
                    || (attackers[j] & opponnentMovesView[i] & PieceMask) === (attackers[j] & PieceMask)) //positions where the attacker is targetting
                    && movesView[i] !== 0) //there are some pieces can attack this position
                {
                    let defenders = getPieces(movesView[i])
                    for (let k = 0; k < defenders.length; k++) {
                        //try to move the defender
                        //find the defender in the board
                        let defenderPos = findPos(defenders[k] | color, boardBuffer)

                        if (testTheWater(boardView, boardBuffer, myKing, defenderPos, i)) {
                            //make the move to save my king
                            moveTo(boardView, boardBuffer, defenderPos, i)
                            // boardView[i] = boardView[defenderPos]
                            // boardView[defenderPos] = 0
                            return boardToScript(boardBuffer)
                        }
                    }
                }
            }
        }
        //something went wrong
        //it should not be here
        //while it's not checkmated, at least one move to save the king exists
        console.log('error in checkmate:', boardToScript(boardBuffer))
        console.log('it should be checkmated!!!')
    }

    console.log('not being checked, make a naive move')
    //capture the target first
    //then, check if we can intercept the path of checking
    //speed up by choosing the right checking path

    //calculate all possible moves 
    let movesBuffer = computeBoard(color, boardBuffer, lastMove)
    // boardView = new Uint32Array(boardBuffer)
    let movesView = new Uint32Array(movesBuffer)
    let availableMoves = []
    for (let i = 0; i < Board.Size; i++) {
        if (movesView[i]) {
            availableMoves.push(i)
        }
    }

    if (availableMoves.length === 0) {
        console.log('no valid move is available.')
        console.log('So, it is drawn!!!', scriptBoard)
        return scriptBoard
    }

    let selectedPos = availableMoves[0]
    if (availableMoves.length > 1) {
        //select a random move in possible moves
        let randomIndex = Math.floor(Math.random() * availableMoves.length)
        selectedPos = availableMoves[randomIndex]
    }

    //check if the selected move do not cause my king being checked
    //test the move
    //get all pieces can move to the selected pos
    let pieces = getPieces(movesView[selectedPos])
    for (let j = 0; j < pieces.length; j++) {
        let piecePos = findPos(pieces[j], boardBuffer)
        //test that move if it causes myKing is checked
        if (testTheWater(boardView, boardBuffer, myKing, piecePos, selectedPos)) {
            //make the move 
            moveTo(boardView, boardBuffer, piecePos, selectedPos)

            // boardView[selectedPos] = boardView[piecePos]
            //en passant --> wrong
            // boardView[piecePos] = 0
            return boardToScript(boardBuffer)
        }
    }
    console.log(`the random move (${selectedPos}) is invalid`)
    console.log(`scan all ${availableMoves.length} possible moves ...`)
    let scanIndex = 0
    do {
        selectedPos = availableMoves[scanIndex]
        console.log(`scanning ${scanIndex} move at ${selectedPos}`)
        //check if the selected move do not cause my king being checked
        //test the move
        //get all pieces can move to the selected pos
        let pieces = getPieces(movesView[selectedPos])
        for (let j = 0; j < pieces.length; j++) {
            let piecePos = findPos(pieces[j], boardBuffer)
            //test that move if it causes myKing is checked
            if (testTheWater(boardView, boardBuffer, myKing, piecePos, selectedPos)) {
                //make the move --> break: return scriptBoard
                moveTo(boardView, boardBuffer, piecePos, selectedPos)
                // boardView[selectedPos] = boardView[piecePos]
                //en passant --> wrong
                // boardView[piecePos] = 0
                return boardToScript(boardBuffer)
            }
        }
        //select another move (next move in the possible moves list)
    } while (++scanIndex < availableMoves.length)//out of move

    console.log('something went wrong:', scriptBoard)
    return scriptBoard
}
function moveTo(boardView, boardBuffer, from, to) {
    if (from === to) return boardBuffer
    // let boardView = new Uint32Array(boardBuffer)

    //Castle
    if (from === 3) {
        console.log('king move', from, to)
    }
    if ((boardView[from] & PieceMask) === Piece.King && (Math.abs(to - from) === 2)) {
        //move the rook to its right place
        console.log('castle')
        let rookPos = (to + from) / 2
        let rookOriginal = from + (rookPos - from) * 4
        if (boardView[from + (rookPos - from) * 3] !== 0) rookOriginal = from + (rookPos - from) * 3
        boardView[rookPos] = boardView[rookOriginal]
        boardView[rookOriginal] = Piece.None
    }

    //en passant
    let { type } = getPieceByValue(boardView[from])
    if (boardView[to] === 0
        && (Math.abs(to - from) === Board.Width - 1 || Math.abs(to - from) === Board.Width + 1)
        && ((type & TypeMask) === Type.Pawn)) {

        let targetPosition = (to > from) ? (to - Board.Width) : (to + Board.Width)
        boardView[targetPosition] = Piece.None
    }

    boardView[to] = boardView[from]
    boardView[from] = Piece.None

    //promotion
    //Pawn in the last row
    if ((type & TypeMask) === Type.Pawn && (to < Board.Width || to >= Board.Size - Board.Width)) {
        //allow select piece for replacement
        let replacePiece = Piece.Queen | (type & ColorMask)
        //-----------------------------------
        // cells[nextPos] = replacePiece
        boardView[to] = replacePiece
    }
    return boardBuffer
}

function coordinateToIndex(coor) {
    // console.log(coor)
    let indices = [...coor]
    let str = 'h'
    if (indices.length === 2) {
        return parseInt(str.charCodeAt(0) - indices[0].charCodeAt(0)) + (parseInt(indices[1]) - 1) * Board.Width;
    }
    return -1;
}

function colRowToIndex(row_col) {
    // console.log(coor)
    // let str = 'a'
    let row = -1
    let col = -1;
    if (row_col.length !== 1) return { row, col };
    console.log('row_col:', row_col)
    let numberLetter1 = '1'
    let numberLetter8 = '8'
    if ((numberLetter8.charCodeAt(0) >= row_col.charCodeAt(0))
        && (numberLetter1.charCodeAt(0) <= row_col.charCodeAt(0))) {
        // console.log('hee')
        row = parseInt(row_col.charCodeAt(0) - numberLetter1.charCodeAt(0));
        return { row, col };
    }
    let str = 'h'
    col = parseInt(str.charCodeAt(0) - row_col.charCodeAt(0));
    return { row, col };
}

function convertPiece(piece, color) {
    piece = color === Color.White ? piece.toLowerCase() : piece.toUpperCase();
    return standardScriptToSpecialScript[piece]
}

function makeAMoveFromScript(color, startBoard, script) {
    // color = Color.Black
    if (color === Color.Black) {
        if (script === 'Kg1') script = 'Kg8'
        else if (script === 'Kc1') script = 'Kc8'
    }
    let boardBuffer = new ArrayBuffer(Board.Size * Board.CellSize)
    boardBuffer = scriptToBoard(startBoard, boardBuffer)
    let movesBuffer = computeBoard(color, boardBuffer)
    let boardView = new Uint32Array(boardBuffer)
    let movesView = new Uint32Array(movesBuffer)

    let { piece, from, to } = moveParser(script)
    let toIndex = coordinateToIndex(to)
    let foundFrom = toIndex
    if (piece !== '') {
        let pieces = convertPiece(piece, color)
        let froms = []
        for (let i = 0; i < pieces.length; i++) {
            let determinedPiece = getPiece(pieces[i])
            if ((determinedPiece.value & PieceMask) === Piece.King) {
                let movedPieces = 0
                canBeCastled(determinedPiece.value, boardBuffer, movesBuffer, movedPieces)
            }
            if ((movesView[toIndex] & determinedPiece.value) === determinedPiece.value) {
                if (from === '') {
                    foundFrom = findPos(determinedPiece.value, boardBuffer)
                    break;
                }
                else {
                    froms.push(findPos(determinedPiece.value, boardBuffer))
                }
            }
        }
        if (from.length === 1) {
            let { row, col } = colRowToIndex(from)
            console.log('row:', row, 'col:', col)
            if (col !== -1)
                froms = froms.filter(e => (e % Board.Width) === col)
            else
                froms = froms.filter(e => parseInt(e / Board.Width) === row)
            console.log('filtered:', froms.length)

            if (froms.length > 0) foundFrom = froms[0]
            else foundFrom = -1
        }
        else if (from.length === 2) {
            console.log('from index:', from)
            let fromIndex = coordinateToIndex(from)
            froms = froms.filter(e => e === fromIndex)
            if (froms.length > 0) foundFrom = froms[0]
            else foundFrom = -1
        }
    }
    else {
        let pieces = getPieces(movesView[toIndex])
        console.log('pawn:')
        console.log(pieces.length)
        pieces.forEach((e) => console.log(getPieceByValue(e).key))

        pieces = pieces.filter(p => getTypeByValue(p) === (Type.Pawn | color))

        console.log('filtered:', pieces.length)
        pieces.forEach((e) => console.log(getPieceByValue(e).key))
        if (from !== '') {
            console.log('process')
        }
        foundFrom = findPos(pieces[0], boardBuffer)
        console.log('pawn position:', getPieceByValue(pieces[0]).key, ' at ', foundFrom)
    }
    if (foundFrom === -1) console.log(startBoard, script, foundFrom, toIndex)
    moveTo(boardView, boardBuffer, foundFrom, toIndex)
    return {lastMove: {from: foundFrom, to: toIndex}, board: boardToScript(boardBuffer)}
}

