export const Board = {
    Width: 8,
    Height: 8,
    Size: 64,
    CellSize: 4//bytes
}
export const isInsideBoard = (index) => index >= 0 && index < Board.Size

export const BoardBuffer = new ArrayBuffer(Board.Size * Board.CellSize)
export const MovesBuffer = new ArrayBuffer(Board.Size * Board.CellSize)
export const resetBuffer = (buffer) => {
    let view = new Uint8Array(buffer)
    view.fill(0)
    return buffer
}

