export function displayMoves(movesBuffer, boardBuffer){
    let movesScript = ''
    let boardView = new Uint32Array(boardBuffer)
    let movesView = new Uint32Array(movesBuffer)
    for (let i=0; i<Board.Size; i++){
        //a: available
        //t: target
        //c: check
        let script = Moves.None
        if (movesView[i] !== Piece.None){
            if (boardView[i] === Piece.None) script = Moves.Available
            else if ((movesView[i] & Color.White) && (boardView[i] & Color.Black)) script = Moves.Target
            else if ((movesView[i] & Color.Black) && (boardView[i] & Color.White)) script = Moves.Target
        }
        movesScript += script
    }
    return movesScript
}
//compute positions to attack
export function computeBoard(color, boardBuffer) {
    let boardView = new Uint32Array(boardBuffer);
    let movesBuffer = new ArrayBuffer(Board.Size * Board.CellSize)
    // let movesView = new Uint32Array(movesBuffer)
    for (let i = 0; i < Board.Size; i++) {
        if (boardView[i] & color) {
            movesBuffer = computeTargets(color, null, movesBuffer, boardBuffer, i)
        }
    }
    return movesBuffer
}
//populate positions to defend
//color === 0 --> Opponents are all other colors.
export function populateBoard(color, boardBuffer) {
    let boardView = new Uint32Array(boardBuffer);
    const movesBuffer = new ArrayBuffer(Board.Size * Board.CellSize)
    // let movesView = new Uint32Array(movesBuffer)
    for (let i = 0; i < Board.Size; i++) {
        if (boardView[i] & color) {
            computeTargets(Color.None, null, movesBuffer, boardBuffer, i)
        }
    }
    return movesBuffer
}
export function computeTargets(color, lastMove, movesBuffer, boardBuffer, curPos) {
    // const movesView = new Uint32Array(curMoves)
    const boardView = new Uint32Array(boardBuffer)
    let curPiece = boardView[curPos]

    //get rules
    //change pawn to attack pawn
    let typeOfPiece = getPieceByValue(curPiece).type

    //mode: 1: attack only, 2: move only, 3: both
    // let attackOnly = false
    if ((typeOfPiece & TypeMask) === Type.Pawn){
        let color = typeOfPiece & ColorMask
        let tempTypeOfPiece = Type.AttackingPawn | color;
        movesBuffer = getMoves(color, movesBuffer, boardBuffer, curPos, tempTypeOfPiece, MoveMode.Attack)
        //check en passant
        if (lastMove){
            // let boardView = new Uint32Array(boardBuffer)
            let movesView = new Uint32Array(movesBuffer)
            //pawn piece, move 2 steps at a time
            if (Math.abs(curPos - lastMove.to) === 1 && (parseInt(curPos / Board.Width) === parseInt(lastMove.to / Board.Width))
                    && Math.abs(lastMove.to - lastMove.from) === Board.Width * 2
                    && getPieceByValue(boardView[lastMove.to]).type === (Type.Pawn | getOpponentColor(color))){
                movesView[parseInt((lastMove.to + lastMove.from)/2)] |= curPiece
            }
        }
        typeOfPiece = getPawnType(curPiece, curPos)
        movesBuffer = getMoves(color, movesBuffer, boardBuffer, curPos, typeOfPiece, MoveMode.Move)
    } 
    else {
        movesBuffer = getMoves(color, movesBuffer, boardBuffer, curPos, typeOfPiece, MoveMode.Both)
    } 
    return movesBuffer
}
export function canBeCastled(kingPiece, boardBuffer, movesBuffer, movedPieces) {
    let color = kingPiece & ColorMask
    let opponentColor = getOpponentColor(color)
    let opponentMovesBuffer = computeBoard(opponentColor, boardBuffer)
    let opponentMovesView = new Uint32Array(opponentMovesBuffer)
    let movesView = new Uint32Array(movesBuffer)
    let boardView = new Uint32Array(boardBuffer)
    let kingPos = (color & Color.White) ? WhiteKingPos : BlackKingPos

    if ((movedPieces & CannotCastleLeftMask) === 0
        && boardView[kingPos - 3] !== 0
        && (getTypeByValue(boardView[kingPos - 3]) === (Type.Rook | color))
        && boardView[kingPos - 1] === 0 && boardView[kingPos - 2] === 0
        && (opponentMovesView[kingPos] === 0)
        && (opponentMovesView[kingPos - 1] === 0)
        && (opponentMovesView[kingPos - 2] === 0)
        && (opponentMovesView[kingPos - 3] === 0)) {
        movesView[kingPos - 2] |= kingPiece
    }
    if ((movedPieces & CannotCastleRightMask) === 0
        && boardView[kingPos + 4] !== 0 && (getTypeByValue(boardView[kingPos + 4]) === (Type.Rook | color))
        && boardView[kingPos + 1] === 0 && boardView[kingPos + 2] === 0 && boardView[kingPos + 3] === 0
        && (opponentMovesView[kingPos] === 0)
        && (opponentMovesView[kingPos + 1] === 0)
        && (opponentMovesView[kingPos + 2] === 0)
        && (opponentMovesView[kingPos + 3] === 0)
        && (opponentMovesView[kingPos + 4] === 0)) {
        movesView[kingPos + 2] |= kingPiece
    }
    return movesBuffer
}

//mode: 1: attack only, 2: move only, 3: both
function getMoves(color, movesBuffer, boardBuffer, pos, typeOfPiece, moveMode){
    const movesView = new Uint32Array(movesBuffer)
    const boardView = new Uint32Array(boardBuffer)
    let curPiece = boardView[pos]
    // let color = typeOfPiece & ColorMask
    let [moves, steps, delta] = ChessRules.get(typeOfPiece);
    var index = 0;
    for (let offset of moves) {
        index = parseInt(pos) + parseInt(offset);
        if (isInsideBoard(index)) {
            let lastPos = pos;
            for (let step of steps) {
                let indexStep = parseInt(pos) + parseInt(offset) * step;
                if (isInsideBoard(indexStep)) {
                    let currentState = checkTarget(color, boardBuffer, lastPos, indexStep, delta);
                    if ((moveMode === MoveMode.Attack && currentState === Moves.Target)
                        || (moveMode === MoveMode.Move && currentState === Moves.Available)
                        || (moveMode === MoveMode.Both && currentState !== Moves.None)){
                        
                        movesView[indexStep] |= curPiece
                    } 
                    if (currentState !== Moves.Available) break;
                    lastPos = indexStep;
                }
            }
        }
    }
    return movesBuffer
}

//is inside the board?
const isInvalidMove = (pos, nextPos, delta) => (Math.abs((pos % 8) - (nextPos % 8)) > delta || Math.abs(Math.floor(pos / 8) - Math.floor(nextPos / 8)) > delta)

function checkTarget(color, curBoard, pos, nextPos, delta) {
    if (isInvalidMove(pos, nextPos, delta)) return Moves.None

    let boardView = new Uint32Array(curBoard)
    if (boardView[nextPos] === Piece.None) return Moves.Available;
    if ((boardView[nextPos] & ColorMask) & getOpponentColor(color)) return Moves.Target
    return Moves.None;
}

export function isCheckmate(color, scriptBoard) {
    let opponentColor = getOpponentColor(color)
    let opponentKing = Piece.King | opponentColor
    let boardBuffer = scriptToBoard(scriptBoard, BoardBuffer)
    let movesBuffer = populateBoard(color, boardBuffer)
    let boardView = new Uint32Array(boardBuffer)
    let movesView = new Uint32Array(movesBuffer)
    let checking = false
    let kingPos = -1
    for (let i = 0; i < Board.Size; i++) {
        if (boardView[i] === opponentKing) {
            if (movesView[i]) {
                kingPos = i
                checking = true
            }
            else
                checking = false
            break
        }
    }
    if (checking) {
        // let checkmate = true
        //check if opponent king can be avoid being checked itself
        let opponnentMovesBuffer = computeBoard(opponentColor, boardBuffer)
        // let boardView = new Uint32Array(boardBuffer)
        let opponnentMovesView = new Uint32Array(opponnentMovesBuffer)

        for (let i = 0; i < Board.Size; i++) {
            if ((opponnentMovesView[i] & PieceMask & Piece.King) === Piece.King
                && movesView[i] === 0) {
                return false //the opponnent king can move to avoid being checked
            }
        }
        //check if there is any piece can intercept the situation
        let attackers = getPieces(movesView[kingPos])
        for (let j = 0; j < attackers.length; j++) {
            //attackers[j]
            for (let i = 0; i < Board.Size; i++) {
                if ((((attackers[j]|color) === boardView[i])
                        ||(attackers[j] & movesView[i] & PieceMask) === (attackers[j] & PieceMask))
                    && opponnentMovesView[i] !== 0) {
                    let defenders = getPieces(opponnentMovesView[i])
                    for (let k = 0; k < defenders.length; k++) {
                        //try to move the defender
                        //find the defender in the board
                        let defenderPos = -1
                        for (let l = 0; l < Board.Size; l++) {
                            if (boardView[l] === (defenders[k] | opponentColor)) {
                                defenderPos = l
                                break;
                            }
                        }
                        //move from defenderPos to i
                        //assume that defenderPos !== i
                        let storedDefenderPosValue = boardView[defenderPos]
                        let storedCapturePosValue = boardView[i]
                        boardView[i] = boardView[defenderPos]
                        boardView[defenderPos] = 0
                        //check if the situation is solved
                        if (!isBeingChecked(boardBuffer, opponentKing)) {
                            // checkmate = false
                            boardView[defenderPos] = storedDefenderPosValue
                            boardView[i] = storedCapturePosValue
                            return false
                        }
                        //restore the board
                        boardView[defenderPos] = storedDefenderPosValue
                        boardView[i] = storedCapturePosValue
                    }
                }
            }
        }
        return true
    }
    //get all attackers
    return false
}
export function isBeingChecked(boardBuffer, kingPiece) {
    let color = kingPiece & ColorMask
    let opponentColor = getOpponentColor(color)
    let movesBuffer = computeBoard(opponentColor, boardBuffer)
    let movesView = new Uint32Array(movesBuffer)
    let boardView = new Uint32Array(boardBuffer)
    for (let i = 0; i < Board.Size; i++) {
        if ((boardView[i]) === kingPiece) {
            if (movesView[i]) return true
            return false
        }
    }
    return false
}

