import React, { useRef, useState, useEffect } from 'react'
import { useSocket } from '../contexts/SocketContext'
import '../css/chessBoard.css'
import {
    Board, PieceMask, ColorMask, TypeMask,
    BoardBuffer, MovesBuffer, resetBuffer,
    ScriptBoard, Piece, Moves, 
    Color, Type,
    canBeCastled,
    scriptToBoard,
    displayMoves,
    computeTargets,
    getPieceByValue,
    // getTypeByValue,
    getKeyByValue,
    getPiece,
    isInsideBoard,
    isBeingChecked
} from '../modules/Chess.js'

import PlayerInfo from './PlayerInfo'

export default function ChessBoard() {
    const { message, inTurn, room, board,
        updateBoardOnServer, notifyReady, notifyLeaving,
        options, lastMove, chatMessage, sendMessage } = useSocket()


    function handleReadyEvent(e) {
        e.preventDefault()
        notifyReady()
    }

    function handleLeaveEvent(e) {
        e.preventDefault()
        notifyLeaving()
    }

    const [activeBoard, setActiveBoard] = useState()
    const [movedPieces, setMovedPieces] = useState(0)
    const [chatHistory, setChatHistory] = useState([])
    const [moveList, setMoveList] = useState([])
    const [curMove, setCurMove] = useState()
    const [moves, setMoves] = useState(ScriptBoard.Blank)
    const [curPos, setCurPos] = useState(-1)//white goes first
    const [whitePiece, setWhitePiece] = useState(false)
    const [nextTurn, setNextTurn] = useState(false)
    const [beingChecked, setChecked] = useState(false)

    const msgRef = useRef()

    function handleSendMessage(e) {
        e.preventDefault()
        sendMessage(msgRef.current.value)
        msgRef.current.value = ''
    }
    // const [playingWhitePiece, setPlayingWhitePiece] = useState(true)
    useEffect(() => {
        setWhitePiece(() => options?.goFirst === true)
        setActiveBoard(() => board)
        setMoveList(current => [...current, lastMove])
        highlightMove(lastMove)
        setChecked(() => {
            let color = options?.goFirst === true ? Color.White : Color.Black
            return isBeingChecked(scriptToBoard(board, BoardBuffer), Piece.King | color)
        })
    }, [board, options, lastMove])
// }, [board, options])

    useEffect(() => {
        setChatHistory(current => ([...current, chatMessage]))
    }, [chatMessage])

    useEffect(() => {
        setMoves(() => getMovesFromScript(lastMove, ScriptBoard.Blank, activeBoard, curPos))
        if (nextTurn) {
            updateBoardOnServer(room, activeBoard, curMove)
            setNextTurn(() => false)
        }
    }, [activeBoard, inTurn, curPos, nextTurn, curMove, getMovesFromScript, lastMove, room, updateBoardOnServer])
// }, [activeBoard, inTurn, curPos, nextTurn])


    function moveTo(scriptBoard, pos, nextPos) {
        if (!isInsideBoard(pos) || !isInsideBoard(nextPos) || pos === nextPos) return scriptBoard
        let boardBuffer = scriptToBoard(scriptBoard, BoardBuffer)
        let boardView = new Uint32Array(boardBuffer)
        let originalPiece = boardView[pos]
        let cells = [...scriptBoard]
        let scriptNone = getKeyByValue(Piece.None)
        
        //Castle
        if ((boardView[pos] & PieceMask) === Piece.King && (Math.abs(nextPos - pos) === 2)) {
            //move the rook to its right place
            let rookPos = (nextPos + pos) / 2
            let rookOriginal = pos + (rookPos - pos) * 4
            if (boardView[pos + (rookPos - pos) * 3] !== 0) rookOriginal = pos + (rookPos - pos) * 3
            cells[rookPos] = cells[rookOriginal]
            cells[rookOriginal] = scriptNone
        }
        
        //en passant
        let {type, color} = getPieceByValue(boardView[pos])
        if (boardView[nextPos] === 0
            && (Math.abs(nextPos - pos) === Board.Width - 1 || Math.abs(nextPos - pos) === Board.Width + 1)
            && ((type & TypeMask) === Type.Pawn)) {
            
            let targetPosition = (nextPos > pos) ? (nextPos - Board.Width) : (nextPos + Board.Width)
            cells[targetPosition] = scriptNone
        }

        cells[nextPos] = cells[pos]
        cells[pos] = scriptNone

        //check if it is a valid move
        if (isBeingChecked(scriptToBoard(cells.join(''), BoardBuffer), Piece.King | color)){
        // if (checkmate(options?.goFirst === true ? Color.White : Color.Black, cells.join(''))) {
            //cancel the last move
            return scriptBoard
        }
        //promotion
        //Pawn in the last row
        if ((type & TypeMask) === Type.Pawn && (nextPos < Board.Width || nextPos >= Board.Size - Board.Width)) {
            //allow select piece for replacement
            let replacePiece = Piece.Queen | (type & ColorMask)
            //-----------------------------------
            // cells[nextPos] = replacePiece
            cells[nextPos] = getKeyByValue(replacePiece)
        }
        // if (isCheckmate(options?.goFirst === true ? Color.White : Color.Black, cells.join(''))) {
        //     alert('hura, won!!!')
        // }
        setCurMove(() => '' + pos + ' - ' + nextPos)
        setMovedPieces(current => {
            let color = options?.goFirst === true ? Color.White : Color.Black
            if ((originalPiece & color) === color) return current | originalPiece
            return current
        })
        return cells.join('')
    }
    function highlightMove(moveScript) {
        // console.log(moveScript)
        return
        // if (moveScript === '' && document.getElementById('board') && parseLastMove(moveScript)) return
        // let { from, to } = parseLastMove(moveScript)
        // Array.from(document.getElementById('board').children).forEach(c => {
        //     c.classList.remove('to')
        //     c.classList.remove('from')
        // })
        // if (to) {
        //     let element = document.getElementById(to)
        //     element.classList.add('to')
        // }
        // if (from) {
        //     let element = document.getElementById(from)
        //     element.classList.add('from')
        // }
    }
    function parseLastMove(moveScript) {
        let moves = moveScript?.split(' - ') ?? null
        if (moves) return { from: parseInt(moves[0]), to: parseInt(moves[1]) }
        return null
    }
    function handleSelectPiece(e) {
        e.preventDefault()
        //check if being in turn
        if (!inTurn) return

        let classes = [...e.target.classList]
        if (classes.includes(Moves.Available) || classes.includes(Moves.Check) || classes.includes(Moves.Target)) {
            let newBoard = moveTo(activeBoard, curPos, parseInt(e.target.getAttribute('data-pos')))
            if (newBoard !== activeBoard) {
                setActiveBoard(curVal => newBoard)
                Array.from(e.target.parentElement.children).forEach(c => c.classList.remove('active'))
                // setTurn(curVal => !curVal)
                setCurPos(curVal => -1)
                setNextTurn(() => true)
            }
            return
        }
        let piece = e.target.getAttribute('data-cell')
        if (whitePiece ? (getPiece(piece).color !== Color.White) : (getPiece(piece).color === Color.White)) return

        Array.from(e.target.parentElement.children).forEach(c => c.classList.remove('active'))
        e.target.classList.add('active')
        setCurPos(parseInt(e.target.getAttribute('data-pos')))
    }
    function getMovesFromScript(lastMoveScript, scriptMoves, scriptBoard, curPos) {
        if (!isInsideBoard(curPos) || !scriptBoard) return scriptMoves

        let boardBuffer = scriptToBoard(scriptBoard, BoardBuffer)
        // let boardView = new Uint32Array(boardBuffer)
        const movesBuffer = resetBuffer(MovesBuffer)
        //check if the king is selected and can be castled
        const boardView = new Uint32Array(boardBuffer)
        if ((boardView[curPos] & PieceMask) === Piece.King) {
            canBeCastled(boardView[curPos], boardBuffer, movesBuffer, movedPieces)
        }
        // let lastMove = parseLastMove(lastMoveScript)
        if (lastMoveScript !== '')
            computeTargets(boardView[curPos] & ColorMask, parseLastMove(lastMoveScript), movesBuffer, boardBuffer, curPos)
        else
            computeTargets(boardView[curPos] & ColorMask, null, movesBuffer, boardBuffer, curPos)
        return displayMoves(movesBuffer, boardBuffer)
    }

    return (
        <>
            <div className='chess-table'>
                <div className='main-board'>
                    <div>board info</div>
                    <PlayerInfo></PlayerInfo>
                    <div id='board' className='board'>
                        {activeBoard && options?.goFirst && [...activeBoard].map((cell, index) => {
                            let cellType = 'white-cell'
                            if ((index + Math.floor(index / 8)) % 2 === 1) cellType = 'black-cell'
                            return <div className={`cell ${cellType} ${getPiece(cell).className} ${moves[index]}`}
                                id={index}
                                key={index} style={{ width: '4rem', height: '4rem' }}
                                data-pos={index}
                                data-cell={cell}
                                onClick={handleSelectPiece}
                            >
                            </div>
                        }).reverse()}
                        {activeBoard && !options?.goFirst && [...activeBoard].map((cell, index) => {
                            let cellType = 'white-cell'
                            if ((index + Math.floor(index / 8)) % 2 === 1) cellType = 'black-cell'
                            return <div className={`cell ${cellType} ${getPiece(cell).className} ${moves[index]}`}
                                id={index}
                                key={index} style={{ width: '4rem', height: '4rem' }}
                                data-pos={index}
                                data-cell={cell}
                                onClick={handleSelectPiece}
                            >
                            </div>
                        })}
                    </div>
                    <PlayerInfo></PlayerInfo>
                    {inTurn && <h1>My turn</h1>}
                    {beingChecked && <p>Being checked</p>}
                    {!beingChecked && <p>Not being checked</p>}
                </div>
                <div className='board-info'>
                    <fieldset>
                        {!options && <button onClick={handleReadyEvent}>Ready</button>}
                        {options && <button onClick={handleLeaveEvent}>Leave</button>}
                        <legend>Message from server:</legend>
                        <span>{message}</span>
                    </fieldset>
                    <div className='moves'>
                        {lastMove && <h2>{lastMove}</h2>}
                        {moveList && moveList.map((move, index) => {
                            return (
                                <>
                                    <h4 key={index}>{move}</h4>
                                </>
                            )
                        }).reverse()}
                    </div>
                    <form onSubmit={handleSendMessage}>
                        <div>
                            <h2>Chat history:</h2>
                            {chatHistory.map((chat, index) => {
                                return (
                                    <>
                                        <div key={index}>
                                            <strong>{chat.sender}:</strong>
                                            {chat.msg}
                                        </div>
                                    </>
                                )
                            })}

                        </div>
                        <label>Message:</label>
                        <input ref={msgRef} type='text' placeholder='say somthing' autoFocus />
                        <button type='submit'>Send</button>
                    </form>
                </div>
            </div>
        </>
    )
}
