const MoveLevel = [
    {key:'!!', value: 'Excelent'},
    {key:'!', value: 'Good'},
    {key:'+-', value: 'White advanced'},
    {key:'-+', value: 'Black advanced'},
    {key:'??', value: 'Awful'},
    {key:'?', value: 'Bad'}
]
const SpecialMove = [
    {key:'O-O', value: 'Near castle'},
    {key:'O-O-O', value: 'Far castle'},
    {key:'=', value: 'Promotion'},
    {key:'+', value: 'Check'},
    {key:'#', value: 'Checkmate'},
    {key:'x', value: 'Capture'}
]
