function moveParser(script){
    //normalize
    //replace capture
    script = script.replaceAll('x', '')
    script = script.replaceAll(':', '')
    //[Piece][from][to][type][comment]
    if (script === 'O-O'){
        script = 'Kg1' //king move to g1
    }
    else if (script === 'O-O-O'){
        script = 'Kc1' //king move to c1
    }

    //remove comment
    let comment = ''
    let index = -1
    for (let i=0; i<MoveLevel.length; i++){
        if ((index = script.indexOf(MoveLevel[i].key)) !== -1)
        {
            comment = script.substr(index)
            script = script.substr(0, index)
        }
    }

    //remove special moves
    let type = ''
    for (let i=0; i<SpecialMove.length; i++){
        if ((index = script.indexOf(SpecialMove[i].key)) !== -1)
        {
            type = script.substr(index)
            script = script.substr(0, index)
        }
    }
    let promote = ''
    if (type.length > 1 && type.indexOf('=') === 0){
        promote = type.substr(1)
    }
    //[Piece][from][to][type][comment]
    //get to
    let to = script.substr(script.length - 2)
    script = script.substr(0, script.length - 2)
    //get piece
    let piece = ''
    if (script.length >= 1){
        piece = script.substr(0, 1)
        let lettera = 'a'
        if (piece.charCodeAt(0) >=  lettera.charCodeAt(0))
            piece = ''
    }
    //get from
    let from = ''
    if (script.length > 1)
        from = script.substr(1)
    return {piece, from, to, type, comment, promote}
}
function parseChessScript(script){
    // let script = `1. d4 d5 2. c4 dxc4 3. e4 Nf6 4. e5 Nd5 5. Bxc4 Nb6 6. Bd3 Nc6 7. Ne2 Bg4 8. f3 Bh5 9. Nbc3 e6 10. Be4 Bg6 11. O-O Be7 12. Be3 Bxe4 13. fxe4 O-O 14. Nf4 Bg5 15. Kh1 Qe7 16. Qh5 h6 17. Rad1 Nc4 18. Bc1 Rad8 19. b3 Nb6 20. Nce2 Rd7 21. Rd3 Rfd8 22. Rg3 Nxd4 23. Nxd4 Rxd4 24. Nxe6 fxe6 25. Bxg5 Qe8 26. Qxe8+ Rxe8 27. Bxh6 Re7 28. Rg4 Nd7 29. Be3 Rd3 30. Bf4 Rd4 31. Re1 c5 32. h3 b5 33. Rg3 Rf7 34. Bc1 c4 35. Kh2 a5 36. bxc4 Rxc4 37. Bb2 Rc2 38. Bd4 Rxa2 39. Rd1 b4 40. Rg6 Nf8 41. Rg3 Rd7 42. Rf3 Ng6 43. Rg3 Kh7 44. Rg5 b3 45. Rh5+ Kg8 46. Rg5 b2 47. Rxg6 Rxd4 48. Rxd4 b1=Q 49. Rd7 Kf8 50. Rdxg7 Ke8 51. Ra7 Kf8 52. Ra8+ Kf7 53. Rag8 1-0`
    let allMoves = []
    let steps = script.split('. ')
    steps.forEach(element => {
        let moves = element.split(' ')
        if (moves.length > 2){
            let white = moveParser(moves[0])
            let black = moveParser(moves[1])
            allMoves.push({white, black})
        }
    });
    return allMoves
}
