import './App.css';
import Signup from './components/Signup'
import { Nav } from 'react-bootstrap';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import { BrowserRouter as Router, Routes, Route, Link, Outlet } from 'react-router-dom'
import Profile from './components/Profile'
import Login from './components/Login';
import CenterLayout from './components/CenterLayout';
import ChessClub from './components/ChessClub';
import UniversalNote from './components/UniversalNote';
import Avatar from './components/Avatar';
import { SocketProvider } from './contexts/SocketContext';
import Home from './components/Home';
import { ClubProvider } from './contexts/ClubContext';

function App() {
  return (
    <Router>
      <AuthProvider>
        <SocketProvider>
        <NavWrapper />
        <ClubProvider><ChessClub /></ClubProvider>
          {
            <Routes>
            <Route path='/' element={<NavWrapper />}>
              <Route exact path='/' Component={Home} />
              {/* <Route path='/chessclub' element={<ClubProvider><ChessClub /></ClubProvider>} />
              <Route path='/notes' Component={UniversalNote} /> */}
              <Route path='/signup' Component={Signup} />
              <Route path='/login' Component={Login} />
              <Route path='/profile' element={<CenterLayout><Profile></Profile></CenterLayout>} />
            </Route>
          </Routes>
          }
        </SocketProvider>
      </AuthProvider>
    </Router>
  );
}
function NavWrapper() {
  const { currentUser } = useAuth()
  return (
    <>
      <nav className='d-flex justify-content-between bg-gradient-light container-fluid'>
          <div className='d-flex flex-row align-items-center justify-content-between' style={{padding: '0.5rem', gap: '1rem'}}>
            <Nav.Item className='nav-item'>
              <a href='/'><img src="./logo.svg" width={'64px'} alt='Logo'/></a>
            </Nav.Item>
            {/* <Nav.Item>
              <Link to='/' className='nav-link'>Home</Link>
            </Nav.Item> */}
            {/* <Nav.Item>
              <Link to='/chessclub' className='nav-link'>Chess Club</Link>
            </Nav.Item>
            <Nav.Item >
              <Link to='/notes' className='nav-link'>Universal Notes</Link>
            </Nav.Item> */}
          </div>
          <div className='d-flex flex-row justify-content-between align-items-center' style={{padding: '0.5rem', gap: '1rem'}}>
            {!currentUser && <Link to='/signup' className='nav-link'>Sign up</Link>}
            {!currentUser && <Link to='/login' className='nav-link'>Log in</Link>}
            {currentUser && <Link to='/profile'>{<Avatar />}</Link>}
          </div>
      </nav>
      <Outlet />
    </>
  )
}
export default App;
