import React from 'react'
import { Container, Card, Button } from 'react-bootstrap'
import { useSocket } from '../contexts/SocketContext'
import { useClub } from '../contexts/ClubContext'
import ChessBoard from './ChessBoard'

export default function Playground() {
    const { setMainContainer } = useClub()
    const { findATable, playWithComputer } = useSocket()
    function handleFindATable(e) {
        e.preventDefault()
        findATable()
        setMainContainer(() => <ChessBoard />)
    }
    function handlePlayWithComputer(e) {
        e.preventDefault()
        playWithComputer()
        setMainContainer(() => <ChessBoard />)
    }
    return (
        <>
            <Container className='d-flex justify-content-around mt-3'>
                <Card>
                    <Card.Header>
                        Play online
                    </Card.Header>
                    <Card.Body>
                        <img width='250px' alt='avatar for playing online' src='images/chess-board.jpg'></img>
                    </Card.Body>
                    <Card.Footer>
                        <Button variant='primary' onClick={handleFindATable}>Find a table</Button>
                    </Card.Footer>
                </Card>
                <Card>
                    <Card.Header>
                        Play with a friend
                    </Card.Header>
                    <Card.Body>
                        <img width='250px' alt='avatar for playing online' src='images/chess-board.jpg'></img>
                    </Card.Body>
                    <Card.Footer>
                        <Button variant='primary'>Invite a friend</Button>
                    </Card.Footer>
                </Card>
                <Card>
                    <Card.Header>
                        Play with computer
                    </Card.Header>
                    <Card.Body>
                        <img width='250px' alt='avatar for playing online' src='images/chess-board.jpg'></img>
                    </Card.Body>
                    <Card.Footer>
                        <Button variant='primary' onClick={handlePlayWithComputer}>Start a Board</Button>
                    </Card.Footer>
                </Card>
            </Container>
        </>
    )
}
