import React from 'react'
import '../css/chessclub.css'
import Playground from './Playground'
import Dashboard from './Dashboard'
import { Container } from 'react-bootstrap'
import { useClub } from '../contexts/ClubContext'
import LearnPlayingChess from './LearnPlayingChess'
import Playback from './Playback'

const CHESS_MODULE = {
    DASHBOARD: 'dashboard',
    PLAYGROUND: 'playground',
    PLAYBACK: 'playback',
    LEARN_PLAYING_CHESS: 'how to play'
}

export default function ChessClub() {

    const { mainContainer, setMainContainer } = useClub()

    function handleClickEvent(e) {
        e.preventDefault()
        showComponent(e.target.getAttribute('data-component'))
    }
    function showComponent(component) {
        switch (component) {
            case CHESS_MODULE.DASHBOARD:
                setMainContainer(() => (<Dashboard />))
                break
            case CHESS_MODULE.PLAYGROUND:
                setMainContainer(() => (<Playground />))
                break
            case CHESS_MODULE.PLAYBACK:
                setMainContainer(() => (<Playback />))
                break
            case CHESS_MODULE.LEARN_PLAYING_CHESS:
                setMainContainer(() => (<LearnPlayingChess />))
                break
            default:
                break
        }
    }
    return (
        <>
            <div className='main-container' style={{ display: 'flex', flexDirection: 'row' }}>
                <div className='left-panel'>
                    <div className='list-item'>
                        <a onClick={handleClickEvent} data-component={CHESS_MODULE.DASHBOARD}>Dashboard</a>
                    </div>
                    <div className='list-item'>
                        <a onClick={handleClickEvent} data-component={CHESS_MODULE.PLAYGROUND}>Playground</a>
                    </div>
                    <div className='list-item'>
                        <a onClick={handleClickEvent} data-component={CHESS_MODULE.PLAYBACK}>Playback</a>
                    </div>
                    <div className='list-item'>
                        <a onClick={handleClickEvent} data-component={CHESS_MODULE.LEARN_PLAYING_CHESS}>How to play</a>
                    </div>
                </div>
                <Container className='w-100'>
                    {mainContainer}
                </Container>
            </div>
        </>
    )
}