import React, { useEffect, useState } from 'react'
import PlayerInfo from './PlayerInfo'
import { ScriptBoard } from '../modals/ChessModal_back'
import { getPiece } from '../modals/ChessModal_back'
import { parseChessScript } from '../modules/Parser'
import '../css/playback.css'
import '../css/chessBoard.css'
import { Color, makeAMoveFromScript } from '../modules/Chess'

export default function Playback() {
    const [board, setBoard] = useState(ScriptBoard.Init)
    const [activeItem, setActiveItem] = useState(-1)
    const [itemCount, setItemCount] = useState(0)
    const [lastMove, setLastMove] = useState({from: -1, to: -1})

    useEffect(() => {
        let movesElement = document.getElementById('script')
        if (movesElement){
            Array.from(movesElement.children).forEach( e => e.classList.remove('active'))
            let activeElement = document.getElementById(`move_${activeItem}`)        
            activeElement?.classList.add('active');
            constructBoard(activeItem)
        }
    }, [activeItem])

    function loadScript(path){
        // fetch(path).then((data) => {
        //     let scripts = parseChessScript(data)
        //     console.log(scripts)
        // })
        // `Lê Quang Liêm vs Fabiano Caruana: 
        // let data = `1. d4 d5 2. c4 dxc4 3. e4 Nf6 4. e5 Nd5 5. Bxc4 Nb6 6. Bd3 Nc6 7. Ne2 Bg4 8. f3 Bh5 9. Nbc3 e6 10. Be4 Bg6 11. O-O Be7 12. Be3 Bxe4 13. fxe4 O-O 14. Nf4 Bg5 15. Kh1 Qe7 16. Qh5 h6 17. Rad1 Nc4 18. Bc1 Rad8 19. b3 Nb6 20. Nce2 Rd7 21. Rd3 Rfd8 22. Rg3 Nxd4 23. Nxd4 Rxd4 24. Nxe6 fxe6 25. Bxg5 Qe8 26. Qxe8+ Rxe8 27. Bxh6 Re7 28. Rg4 Nd7 29. Be3 Rd3 30. Bf4 Rd4 31. Re1 c5 32. h3 b5 33. Rg3 Rf7 34. Bc1 c4 35. Kh2 a5 36. bxc4 Rxc4 37. Bb2 Rc2 38. Bd4 Rxa2 39. Rd1 b4 40. Rg6 Nf8 41. Rg3 Rd7 42. Rf3 Ng6 43. Rg3 Kh7 44. Rg5 b3 45. Rh5+ Kg8 46. Rg5 b2 47. Rxg6 Rxd4 48. Rxd4 b1=Q 49. Rd7 Kf8 50. Rdxg7 Ke8 51. Ra7 Kf8 52. Ra8+ Kf7 53. Rag8 1-0`
        // `Sergey Karjakin vs Lê Quang Liêm: 
        // let data = `1. e4 e5 2. Nf3 Nc6 3. Bc4 Bc5 4. O-O Nf6 5. d3 O-O 6. h3 h6 7. c3 d6 8. Re1 a5 9. Nbd2 Be6 10. Bb5 Ba7 11. Nf1 Ne7 12. d4 Ng6 13. Ng3 c6 14. Bd3 a4 15. Be3 b5 16. Qc2 Re8 17. c4 bxc4 18. Bxc4 Bxc4 19. Qxc4 d5 20. Qxc6 exd4 21. Bxd4 Bxd4 22. Nxd4 Ne5 23. Qc3 Rc8 24. Qd2 Nc4 25. Qb4 dxe4 26. Ndf5 Qd7 27. Rad1 Nd5 28. b3 Nxb4 29. Rxd7 Ne5 30. Ne7+ Kf8 31. Nxc8 Nxd7 32. Nd6 axb3 33. axb3 Rb8 34. Ngxe4 Nd3 35. Rb1 Rb4 36. Nc4 N7c5 37. Nxc5 Nxc5 38. Nd2 Rd4 39. Rb2 g6 40. Kf1 Rd7 41. Rc2 Ke7 42. Ke2 Ne6 43. Rc4 Nd4+ 44. Ke1 Ke6 45. g3 Kd5 46. Rc3 Ra7 47. Kf1 Ra2 48. Nf3 Nxf3 49. Rxf3 Ra1+ 50. Ke2 Ke6 51. Re3+ Kf6 52. Kd3 Ra2 53. f4 g5 54. fxg5+ hxg5 55. g4 Rf2 56. Kc4 Kg6 57. b4 f5 58. gxf5+ Kxf5 59. b5 Kf4 60. Rb3 Rc2+ 61. Kd5 Rd2+ 62. Kc6 Rc2+ 63. Kd7 Rd2+ 64. Kc7 Rc2+ 65. Kb8 Ke5 66. b6 Kd6 67. b7 Kd7 68. Ra3 Rb2 69. Ka8 1-0`
        // `Lê Quang Liêm vs Hikaru Nakamura: 
        let data = `1. d4 Nf6 2. c4 e6 3. Nf3 d5 4. g3 Bb4+ 5. Bd2 Be7 6. Bg2 O-O 7. O-O Nbd7 8. Qc2 Ne4 9. Bf4 c6 10. Nc3 g5 11. Bc1 f5 12. b3 b6 13. Bb2 Bb7 14. Rad1 Qe8 15. Ne5 Nxe5 16. dxe5 Nxc3 17. Qxc3 Rc8 18. e3 Qg6 19. Qc1 Qg7 20. f4 gxf4 21. gxf4 Kh8 22. Kh1 Rg8 23. Bf3 Qh6 24. Ba3 Bxa3 25. Qxa3 Rg7 26. Qb2 Rcg8 27. Rg1 Qh4 28. Rxg7 Rxg7 29. Qd2 Ba6 30. cxd5 cxd5 31. Rg1 Rc7 32. Qg2 Qd8 33. Qh3 Rg7 34. Qh6 Rxg1+ 35. Kxg1 d4 36. Qxe6 dxe3 37. Qd6 Qg8+ 38. Bg2 Qg4 39. e6 e2 40. Qe5+ Qg7 41. Kf2 Qxe5 42. fxe5 Kg7 43. Bf3 Kf8 44. Bxe2 Bc8 45. Bc4 Ke7 46. Ke3 Bb7 47. Bd3 h6 48. Bxf5 Bc6 49. b4 Bd5 50. a3 Bb3 51. Bg4 Ba4 52. Kf4 Bc2 53. Kg3 Bd3 54. Kh4 Bg6 55. Bh5 Bc2 56. Bg4 Bg6 57. Kg3 Bc2 58. Kf4 Bd3 59. Ke3 Bc2 60. Kd4 Bb3 61. Bf5 Bd1 62. Kd5 Ba4 63. Bd3 Be8 64. b5 Bh5 65. Kc6 Bf3+ 66. Kc7 Kxe6 67. Kb8 Kxe5 68. Kxa7 Kd4 69. Kxb6 Kxd3 70. Kc5 Ke4 71. a4 Ke5 72. a5 Ke6 73. Kb6 Be2 74. Kc6 Ke7 75. b6 1-0`
        let scripts = parseChessScript(data)
        return scripts
    }
    function handleLoadEvent(e){
        e.preventDefault()
        let path = ''
        let scripts = loadScript(path)
        console.log(scripts.length)
        setItemCount(() => scripts.length * 2)
        Array.from(scripts).forEach((element, index) => {
            let newElement = document.createElement('div')
            newElement.id = `move_${index*2}`
            newElement.className = 'list-item'
            let movesElement = document.getElementById('script')
            let whiteScript = `${element.white.piece}${element.white.from}${element.white.to}${element.white.type}${element.white.comment}`
            let blackScript = `${element.black.piece}${element.black.from}${element.black.to}${element.black.type}${element.black.comment}`
            newElement.textContent = index+1 + '. ';
            newElement.setAttribute('data-script-index', index*2)
            newElement.setAttribute('data-script-white', whiteScript)
            newElement.setAttribute('data-script-black', blackScript)
            
            let subItem1 = document.createElement('div')
            subItem1.id = `move_${index}_0`
            subItem1.textContent = `${whiteScript}`
            newElement.appendChild(subItem1)
            
            let subItem2 = document.createElement('div')
            subItem2.id = `move_${index}_1`
            subItem2.textContent = `${blackScript}`
            newElement.appendChild(subItem2)

            movesElement.appendChild(newElement)
            newElement.addEventListener('click', handleClickEvent)
        })
        setActiveItem(() => 0)
    }
    function handlePreEvent(e){
        e.preventDefault()
        setActiveItem(current => {
            if (current > 1) return current - 1
            return current
        })
    }
    function handleNextEvent(e){
        e.preventDefault()
        setActiveItem(current => {
            if (current < itemCount - 1) return current + 1
            return current
        })
    }
    function handleClickEvent(e){
        e.preventDefault()
        let target = e.currentTarget
        let index = target.getAttribute('data-script-index')
        setActiveItem(() => parseInt(index))
    }
    function constructBoard(index){
        if (index < 0) return;
        // let board = ScriptBoard.Init
        let itemIndex = parseInt(index / 2);
        let subItemIndex = parseInt(index % 2);
        let result = {lastMove: {from: -1, to: -1}, board: ScriptBoard.Init}
        for (let i=0; i<itemIndex; i++){
            let target = document.getElementById(`move_${i*2}`)
            result = makeAMoveFromScript(Color.White, result.board, target.getAttribute('data-script-white'))
            result = makeAMoveFromScript(Color.Black, result.board, target.getAttribute('data-script-black'))
        }
        let target = document.getElementById(`move_${itemIndex*2}`)
        result = makeAMoveFromScript(Color.White, result.board, target.getAttribute('data-script-white'))
        if (subItemIndex === 1) result = makeAMoveFromScript(Color.Black, result.board, target.getAttribute('data-script-black'))
        setLastMove(() => result.lastMove)
        setBoard(() => result.board)
    }
    return (
        <>
            <div className='chess-table'>
                <div className='main-board'>
                    <div>board info</div>
                    <PlayerInfo></PlayerInfo>
                    <div id='board' className='board'>
                        {[...board].map((cell, index) => {
                            let cellType = 'white-cell'
                            if ((index + Math.floor(index / 8)) % 2 === 1) cellType = 'black-cell'
                            let fromClass = ''
                            if (lastMove && lastMove.from === index) fromClass = 'from'
                            let toClass = ''
                            if (lastMove && lastMove.to === index) fromClass = 'to'
                            return <div className={`cell ${cellType} ${getPiece(cell).className} ${fromClass} ${toClass}`}
                                id={index}
                                key={index} style={{ width: '4rem', height: '4rem' }}
                                data-pos={index}
                                data-cell={cell}
                                // onClick={handleSelectPiece}
                            >
                            </div>
                        }).reverse()}
                    </div>
                    <PlayerInfo></PlayerInfo>
                    {/* {inTurn && <h1>My turn</h1>}
                    {beingChecked && <p>Being checked</p>}
                    {!beingChecked && <p>Not being checked</p>} */}
                </div>
                <div className='board-info'>
                    <button onClick={handleLoadEvent}>Load script</button>
                    <button onClick={handlePreEvent}>Pre</button>
                    <button onClick={handleNextEvent}>Next</button>
                    <div id="script" className='moves'>
                    </div>
                </div>
            </div>
        </>
    )
}
